
import "./styles.css"

import { useState, useEffect } from "react";
import useSound from "use-sound";

import BGM0 from "../../sfx/menu-music/AC7-title.wav";
import BGM1 from "../../sfx/menu-music/AC2-Hanger.mp3";
import BGM2 from "../../sfx/menu-music/AC2-Invoke.mp3";
import BGM3 from "../../sfx/menu-music/AC2-Lode Stone.mp3";
import BGM4 from "../../sfx/menu-music/AC3-briefing.mp3";
import BGM5 from "../../sfx/menu-music/AC3-linkage.mp3";
import BGM6 from "../../sfx/menu-music/AC4-Scramble.mp3";
import BGM7 from "../../sfx/menu-music/AC4-The Hangar.mp3";
import BGM8 from "../../sfx/menu-music/AC5-Hangar1.mp3";
import BGM9 from "../../sfx/menu-music/AC5-menu.mp3";
import BGM10 from "../../sfx/menu-music/AC5-Tutorial Main.mp3";
import BGM11 from "../../sfx/menu-music/AC6-menu.mp3";
import BGM12 from "../../sfx/menu-music/AC7-444th-Base.mp3";
import BGM13 from "../../sfx/menu-music/ACAH-hangar2.mp3";
import BGM14 from "../../sfx/menu-music/ACAH-refrain.mp3";
import BGM15 from "../../sfx/menu-music/ACX-aircraft_select.mp3";
import BGM16 from "../../sfx/menu-music/ACZ-Briefing1.mp3";
import BGM17 from "../../sfx/menu-music/ACZ-Hangar.mp3";
import BGM18 from "../../sfx/menu-music/ACZ-Sortie1.mp3";
import BGM19 from "../../sfx/menu-music/AC6-Sortie3.mp3";
import BGM20 from "../../sfx/menu-music/AC6-menu2.mp3";

import NavigateSfx from "../../sfx/navigate.mp3";
import SelectSfx from "../../sfx/select.mp3";

import { FaPlayCircle, FaChevronDown, FaBackward, FaForward, FaPlay, FaPause } from "react-icons/fa";

const Music = ({ musicState }) => {

	const [ playNavigate ] = useSound( NavigateSfx, {volume:1} );
	const [ playSelect ] = useSound( SelectSfx, {volume:1} );

	const [ playerStatus, setPlayerStatus ] = useState(false);
	const [trackPlayingName, setTrackPlayingName] = useState("--");
	const [trackPlayingAlbum, setTrackPlayingAlbum] = useState("--");
	const [trackPlayingThumbnail, setTrackPlayingThumbnail] = useState("--");
	const [showMiniPlayer, setShowMiniPlayer] = useState(true);

	const [showFullCompPlayer, setShowFullCompPlayer] = useState(false);
	
	const shuffle = (array) => {
		let currentIndex = array.length, randomIndex;
		// While there remain elements to shuffle...
		while (currentIndex !== 0) {
			// Pick a remaining element...
			randomIndex = Math.floor(Math.random() * currentIndex);
			currentIndex--;

			// And swap it with the current element.
			[array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]];
		}
		return array;
	}

	let aczAlbum = {name:"Ace Combat Zero: The Belkan War", thumbnail:"acz.jpg"};
	let ac2Album = {name:"Ace Combat 2", thumbnail:"ac2.png"};
	let ac3Album = {name:"Ace Combat 3: Electrosphere", thumbnail:"ac3.jpg"};
	let ac4Album = {name:"Ace Combat 4: Shattered Skies", thumbnail:"ac4.jpg"};
	let ac5Album = {name:"Ace Combat 5: The Unsung War", thumbnail:"ac5.jpg"};
	let ac6Album = {name:"Ace Combat 6: Fires of Liberation", thumbnail:"ac6.png"};
	let ac7Album = {name:"Ace Combat 7: Skies Unknown", thumbnail:"ac7.webp"};
	let acxAlbum = {name:"Ace Combat X: Skies of Deception", thumbnail:"acx.jpg"};
	let acahAlbum = {name:"Ace Combat Assault Horizon", thumbnail:"acah.jpg"};

	let trackList = [
		{
			track:BGM0,
			name:"Skies Unknown",
			album:ac7Album.name,
			thumbnail:ac7Album.thumbnail
		},{
			track:BGM1,
			name:"Hangar",
			album:ac2Album.name,
			thumbnail:ac2Album.thumbnail
		},{
			track:BGM2,
			name:"Invoke",
			album:ac2Album.name,
			thumbnail:ac2Album.thumbnail
		},{
			track:BGM3,
			name:"Lode Stone",
			album:ac2Album.name,
			thumbnail:ac2Album.thumbnail
		},{
			track:BGM4,
			name:"Briefing",
			album:ac3Album.name,
			thumbnail:ac3Album.thumbnail
		},{
			track:BGM5,
			name:"Linkage",
			album:ac3Album.name,
			thumbnail:ac3Album.thumbnail
		},{
			track:BGM6,
			name:"Scramble",
			album:ac4Album.name,
			thumbnail:ac4Album.thumbnail
		},{
			track:BGM7,
			name:"The Hangar",
			album:ac4Album.name,
			thumbnail:ac4Album.thumbnail
		},{
			track:BGM8,
			name:"Hangar 1",
			album:ac5Album.name,
			thumbnail:ac5Album.thumbnail
		},{
			track:BGM9,
			name:"Menu",
			album:ac5Album.name,
			thumbnail:ac5Album.thumbnail
		},{
			track:BGM10,
			name:"Tutorial Main",
			album:ac5Album.name,
			thumbnail:ac5Album.thumbnail
		},{
			track:BGM11,
			name:"Menu",
			album:ac6Album.name,
			thumbnail:ac6Album.thumbnail
		},{
			track:BGM12,
			name:"444 Base",
			album:ac7Album.name,
			thumbnail:ac7Album.thumbnail
		},{
			track:BGM13,
			name:"Hangar 2",
			album:acahAlbum.name,
			thumbnail:acahAlbum.thumbnail
		},{
			track:BGM14,
			name:"Refrain",
			album:acahAlbum.name,
			thumbnail:acahAlbum.thumbnail
		},{
			track:BGM15,
			name:"Aircraft Select",
			album:acxAlbum.name,
			thumbnail:acxAlbum.thumbnail
		},{
			track:BGM16,
			name:"Briefing 1",
			album:aczAlbum.name,
			thumbnail:aczAlbum.thumbnail
		},{
			track:BGM17,
			name:"Hangar",
			album:aczAlbum.name,
			thumbnail:aczAlbum.thumbnail
		},{
			track:BGM18,
			name:"Sortie 1",
			album:aczAlbum.name,
			thumbnail:aczAlbum.thumbnail
		},{
			track:BGM19,
			name:"Sortie 3",
			album:ac6Album.name,
			thumbnail:ac6Album.thumbnail
		},{
			track:BGM20,
			name:"Menu 2",
			album:ac6Album.name,
			thumbnail:ac6Album.thumbnail
		}
	];
	

	let currentTrackIndex = 0;

	shuffle( trackList );

	const audio = new Audio( trackList[ currentTrackIndex ].track );
	audio.volume = 0.4;
	
	useEffect(() => {
		setTrackPlayingName( trackList[ currentTrackIndex ].name );
		setTrackPlayingAlbum( trackList[ currentTrackIndex ].album );
		setTrackPlayingThumbnail( trackList[ currentTrackIndex ].thumbnail );
		
		document.addEventListener("keydown", togglePlay);
		audio.addEventListener("ended", playNextSong);

		setShowFullCompPlayer( true );
		//playPauseMusic();
	}, [musicState]);

	const togglePlay = (event) => {
		if (event.keyCode === 118) {
			event.preventDefault();
			playPrevSong();
		} else if (event.keyCode === 119) {
			event.preventDefault();
			playPauseMusic();
		} else if (event.keyCode === 120) {
			event.preventDefault();
			playNextSong();
		}
	}

	const playPauseMusic = () => {
		musicState = !musicState;
		musicState ? audio.play() : audio.pause();
		setPlayerStatus( musicState );
		console.log( musicState );
	}

	const playPrevSong = () => {
		audio.pause();
		
		if (currentTrackIndex > 0) {
			currentTrackIndex = currentTrackIndex - 1;
		} else if (currentTrackIndex === 0) {
			currentTrackIndex = 19;
		}

		setTrackPlayingName( trackList[ currentTrackIndex ].name );
		setTrackPlayingAlbum( trackList[ currentTrackIndex ].album );
		setTrackPlayingThumbnail( trackList[ currentTrackIndex ].thumbnail );
		audio.src = trackList[ currentTrackIndex ].track;
		audio.play();
	}

	const playNextSong = () => {
		audio.pause();
		
		if (currentTrackIndex < 20) {
			currentTrackIndex = currentTrackIndex + 1;
		} else if (currentTrackIndex === 20) {
			currentTrackIndex = 0;
		}

		setTrackPlayingName( trackList[ currentTrackIndex ].name );
		setTrackPlayingAlbum( trackList[ currentTrackIndex ].album );
		setTrackPlayingThumbnail( trackList[ currentTrackIndex ].thumbnail );
		audio.src = trackList[ currentTrackIndex ].track;
		audio.play();
	}

	const toggleMiniPlayer = () => {
		let newVal = showMiniPlayer;
        setShowMiniPlayer(!newVal);
		playSelect();
	}


	/*
	<div className="tw-px-2">
					<span className="music-player-icon tw-text-2xl" onClick={() => playPrevSong()} onMouseEnter={ playNavigate }>
						<FaBackward />
					</span>
				</div>
				<div className="tw-px-2">
					<span className="music-player-icon tw-text-2xl" onClick={() => playPauseMusic()} onMouseEnter={ playNavigate }>
						<FaPlay className={`${musicState ? '' : 'tw-hidden'}`}/>
						<FaPause className={`${!musicState ? '' : 'tw-hidden'}`}/>
					</span>
				</div>
				<div className="tw-px-2">
					<span className="music-player-icon tw-text-2xl" onClick={() => playNextSong()} onMouseEnter={ playNavigate }>
						<FaForward />
					</span>
				</div>
	*/
	return (
		<div className={`music-player ${showFullCompPlayer ? '' : 'tw-hidden'} ${!showMiniPlayer ? 'music-player-fab' : ''}`} onKeyPress={togglePlay}>
			<span className={`music-player-icon tw-text-2xl ${!showMiniPlayer ? '' : 'tw-hidden'}`} onClick={() => toggleMiniPlayer()} onMouseEnter={ playNavigate }>
				<FaPlayCircle />
			</span>

			<div className={`music-player-track tw-flex tw-flex-row tw-items-center tw-px-3 tw-py-1 ${showMiniPlayer ? '' : 'tw-hidden'}`}>
				
				<div className="tw-px-2">
					<img src={process.env.PUBLIC_URL + `/albumart/${trackPlayingThumbnail}`} alt="thumbnail" height="56" width="56"/>
				</div>
				<div className="tw-flex tw-flex-col tw-px-2">
					<h6 className={`${playerStatus ? '' : 'tw-hidden'}`}>Now Playing:</h6>
					<h6 className={`${!playerStatus ? '' : 'tw-hidden'}`}>Paused:</h6>
					<h3>{ trackPlayingName }</h3>
					<h5>{ trackPlayingAlbum }</h5>
				</div>
				<div className="tw-px-2">
					<span className="music-player-icon tw-text-2xl" onClick={toggleMiniPlayer} onMouseEnter={ playNavigate }>
						<FaChevronDown />
					</span>
				</div>
			</div>
		</div>
	)
}

export default Music
